import {
  humanizePlanName,
  isPlanEnterpriseTrial,
  normalizeTeamPlanName,
} from './plans';

export type AccountPlanData = {
  on_pro_trial: boolean;
  type_name: string;
  type_slug: string;
};

export const getAccountNormalizedPlanName = (account?: AccountPlanData) => {
  const planName = account?.type_name;
  const normalizedPlanName = normalizeTeamPlanName(planName);
  const isTrial = isPlanEnterpriseTrial(account?.type_slug);
  return `${normalizedPlanName}${isTrial ? ' (free month)' : ''}`;
};

export const getAccountPlanTitle = (account?: AccountPlanData) =>
  humanizePlanName(getAccountNormalizedPlanName(account));
